.random-word-01w {
  min-height: 100vh;
  padding-top: 40px;
  text-align: center;

  background: #d6d6d6;

  & > .box {
    display: inline-block;
    background: white;
    padding: 35px;
    border-radius: 5px;
    border: 1px solid #d6d6d6;
    box-shadow: 2px 2px 4px rgba(black, .1);
    font-weight: 600;
    font-size: 30px;
    user-select: none;
  }
}