.word-list-9yf {
  & > .word {

    user-select: none;
    font-weight: bold;
    color: #9b5e27;

    display: inline-block;
    padding: 5px 5px;

    @media screen and (min-width: 768px) {
      display: block;
      padding: 5px 15px;
    }

    cursor: pointer;
    &:hover {
      background: #f6f6f6;
    }

    &.selected {
      //text-decoration: line-through;
      background: #d8fad8;
      color: #037203;
    }

    &.disabled {
      color: #d4d4d4;
      background: none;
      cursor: default;
    }
  }
}