.typing-1-1ws {

  min-height: 100vh;
  padding-top: 40px;
  text-align: center;

  background: #d6d6d6;

  & > .main {
    display: inline-block;
    background: white;
    padding: 35px;
    border-radius: 5px;
    border: 1px solid #d6d6d6;
    box-shadow: 2px 2px 4px rgba(black, .1);
    max-width: calc(100% - 30px);

    & > .sample {
      margin-bottom: 30px;
    }

    & > textarea {
      width: 100%;
      height: 300px;
    }
  }
}